.header{
    background-color: #010141;
}
.child{
    padding: 10px 30px;
    color: #ffffff;
}
.formStyleMain h3{
    font-size: 28px;
}
.formStyle{
    display: flex;
    flex-direction: column;
}
.formStyle b{
    color: red;
    position: relative;
    cursor: default;
}
.formStyle b:hover::before{
    display: block;
}
.formStyle b::before{
    content: "Обязательное поле";
    position: absolute;
    background-color: rgb(255, 255, 255);
    border: 1px solid #010141;
    color: black;
    padding: 3px;
    border-radius: 5px;
    font-size: 14px;
    display: none;
    top: 0;
    left: 10px;
}
.formStyle span{
    font-size: 20px;
    margin-top: 8px;
}
.formStyleInputText{
    outline: none;
    border: 1px solid #010141;
    border-radius: 5px;
    font-size: 20px;
    color: #010141;
}
.marginT{
    margin-top: 10px;
    margin-bottom: 10px;
}
.formStyleInputTextarea{
    outline: none;
    border: 1px solid #010141;
    border-radius: 5px;
    font-size: 20px;
    color: #010141;
    resize: none;
}
.example{
    display: flex;
    height: 50px;
    position: relative;
    border: 2px dashed #000;
    margin-top: 10px;
    padding: 5px;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
}

.label>div{
    display: flex;
}
.materialicons{

}
.title{
    margin-right: 5px;
}
.example input{
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    width: 100%;
    height: 60px;
    background-color: transparent;
    cursor: pointer;
}
.arrow_group{
    position: relative;
    height: 45px;
    width: 50px;
    top: 0;

}
.arrow{
    position: absolute;
    width: auto;
    height: 25px;
    top: 0;
    left: 11px;

}
.arrowAnime{

    animation-duration: 0.7s;
    animation-name: move_ar;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: linear;
}
@keyframes move_ar { from { top: 0; } to { top: 14px; }  }
.arrow_group span{
    position: absolute;
    display: block;
    width: 50px;
    height: 6px;
    border-radius: 15px;
    background-color: black;
    bottom: 0;
}
.borderStyle{
    border-color: #575757;
}

.notification{
    position: sticky;
    top: 10px;
    right: 0;
    font-size: 18px;
    padding: 10px;
    display: flex;
    border: 1px solid red;
    background-color: rgba(255, 255, 255, 1);
}
.closeButton{
    position: absolute;
    right: 5px;
    top: 5px;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid red;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s ease;
}
.closeButton:hover{
    background-color: red;
    color: #fff;
}
.titleSuccessfulCon{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.titleSuccessfulAdd{
    margin: 15px 0;
    text-align: center;
    font-size: 28px;
}
.titleSuccessfulButton{
    background-color: #fff;
    border: 1px solid #010141;
    border-radius: 10px;
    font-size: 20px;
    outline: none;
    cursor: pointer;
    padding: 3px 8px;
    transition: all 0.3s ease;
}
.titleSuccessfulButton:hover{
    background-color: #010141;
    color: #fff;
}