.main{
    display: flex;
    flex-direction: column;
}
.formStyleInputTextarea{
    outline: none;
    border: 1px solid #010141;
    border-radius: 5px;
    font-size: 20px;
    color: #010141;
    resize: none;
}

.formStyleInputText{
    outline: none;
    border: 1px solid #010141;
    border-radius: 5px;
    font-size: 20px;
    color: #010141;
}
.element{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.element span{
    font-size: 22px;
    margin-right: 10px;
    color: #000;
}
.element {
    font-size: 22px;
    color:  #010141;
    cursor: default;
    user-select: none;
}
.areaCon{
    display: flex;
    flex-direction: column;

}
.areaCon p{
    font-size: 18px;
    color:  #010141;
    cursor: default;
    user-select: none;
}
.areaCon span{
    font-size: 22px;
}

.buttonGroup{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.buttonSave{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    padding: 0 10px;
    font-size: 24px;

    background-color: #fff;
    border: 1px solid #010141;
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease;

    margin-bottom: 5px;
}
.buttonSave:hover{
    background-color: #010141;
    border-color: #010141;
    color: #fff;
}
.buttonCansler{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    padding: 0 10px;
    font-size: 24px;

    background-color: #fff;
    border: 1px solid #010141;
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease;

    margin-bottom: 5px;
}
.buttonCansler:hover{
    background-color: red;
    border-color: red;
    color: #fff;
}

.document{
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}
.document span{
    display: flex;
    flex-direction: row;
    font-size: 22px;

    color: #000;
}
.document input{

}


/**************************/
.example{
    display: flex;
    height: 50px;
    position: relative;
    border: 2px dashed #000;
    margin-top: 10px;
    padding: 5px;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
}

.label>div{
    display: flex;
}
.materialicons{

}
.title{
    margin-right: 5px;
}
.example input{
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    width: 100%;
    height: 60px;
    background-color: transparent;
    cursor: pointer;
}
.arrow_group{
    position: relative;
    height: 45px;
    width: 50px;
    top: 0;

}
.arrow{
    position: absolute;
    width: auto;
    height: 25px;
    top: 0;
    left: 11px;

}
.arrowAnime{

    animation-duration: 0.7s;
    animation-name: move_ar;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: linear;
}
@keyframes move_ar { from { top: 0; } to { top: 14px; }  }
.arrow_group span{
    position: absolute;
    display: block;
    width: 50px;
    height: 6px;
    border-radius: 15px;
    background-color: black;
    bottom: 0;
}
.borderStyle{
    border-color: #575757;
}


