.Main{
    margin-top: 15px;
}
.Container{
    max-width: 1200px;
    margin: 0 auto;
}
.child{
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-areas:
            "left menu";
    grid-gap: 20px;

}
.left{
    grid-area: left;
    order: 2;
    background-color: #ffff;
    padding: 10px;
}
.menu{
    grid-area: menu;
    order: 1;
}
.elem_menu{
    display: flex;
    color: black;
    text-decoration: none;
    background-color: #fff;
    text-align: left;
    padding: 10px 20px;
    border-left: 5px solid  #010141;
    font-size: 18px;
    margin-bottom: 10px;
    transition: all 0.3s ease;
}
.elem_menu:hover{
    border-left-color: rgba(1, 1, 65, 0.32);
}
@media screen and (max-width: 900px) {
    .child{
        display: flex;
        flex-direction: column;
    }
    .elem_menu{
        margin: 0 25px;
    }
}