.Main{
    display: flex;
    flex-wrap: wrap;
}
.elem{
    width: 50%;
}
.elem_container{
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin: 10px;
    border: 1px solid #000;
}
.name{
    font-size: 20px;
    max-width: 280px;
    overflow: hidden;
}
.fio{
    max-width: 280px;
    overflow: hidden;
}
.button_group{
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.edit:hover{
    background-color: #010141;
    color: #fff;
}
.delete:hover{
    background-color: #bd0000;
    color: #fff;
    border-color: #bd0000;
}
.button{
    text-decoration: none;
    background-color: #fff;
    color: black;
    border: 1px solid #010141;
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    padding: 3px 7px;
    transition: all 0.3s ease;
    margin-bottom: 2px;
}
@media screen and (max-width: 700px){
    .elem{
        width: 100%;
    }
}



